/* eslint-disable import/prefer-default-export */
import { ref, computed, ComputedRef } from 'vue';

const loading = ref(false);
const serverError = ref('');

interface UsedAddItemState {
  isLoading: ComputedRef<boolean>,
  setLoading(value: boolean): void,
  getServerError(): string,
  setServerError(value: string): void,
  resetAddItemModalValue(): void,
}

export function useAddItemState(): UsedAddItemState {
  const isLoading = computed(() => loading.value);
  const setLoading = (value: boolean) => { loading.value = value; };

  const getServerError = () => serverError.value;
  const setServerError = (value: string) => { serverError.value = value; };

  const resetAddItemModalValue = () => {
    setServerError('');
    setLoading(false);
  };

  return {
    isLoading,
    setLoading,
    getServerError,
    setServerError,
    resetAddItemModalValue,
  };
}
