
import {
  defineComponent, ref, computed, watch,
} from 'vue';

export default defineComponent({
  props: {
    expiration: {
      type: Number,
      default: 0,
    },
  },
  setup(props) {
    const currentDate = new Date();
    const date = new Date(props.expiration - currentDate.getTime());
    const hours = ref(date.getHours() - 1);
    const minutes = ref(date.getMinutes());
    const seconds = ref(date.getSeconds());
    const isValid = computed(() => !(
      hours.value === 0 && minutes.value === 0 && seconds.value === 0));

    const intervalValue = setInterval(() => {
      if (seconds.value === 0) {
        minutes.value = minutes.value === 0 ? 60 : (minutes.value - 1);
      }
      seconds.value = seconds.value <= 0 ? 60 : (seconds.value - 1);
    }, 1000);
    // do something

    watch(isValid, (value) => {
      if (!value) {
        clearInterval(intervalValue);
      }
    });

    const doubleDigit = (digit: number): string | number => (digit < 10 ? `0${digit}` : digit);

    return {
      isValid,
      hours,
      minutes,
      seconds,
      doubleDigit,
    };
  },
});
