<template>
  <div class="todo" :class="classes">
    <div class="todo__el" v-if="editMode">
      <icon-button @click="showAddItem = true">
        <img src="@/assets/images/icons/edit.svg" alt="edit">
      </icon-button>
    </div>
    <div class="todo__el" v-if="viewMode">
      <tool-tip>
        <template #element>
          <button @click="setStatus" class="todo__el todo__done-btn">
            <img
              v-if="isDone"
              src="@/assets/images/idea-highlighted.svg"
              alt="done"
              class="todo__img"
            />
            <img
              v-else
              src="@/assets/images/idea.svg"
              alt="open"
              class="todo__img"
            />
          </button>
        </template>
        <template #text>
          {{markText}}
        </template>
      </tool-tip>
    </div>
    <div class="todo__content todo__el">
      <p class="todo__description">{{todo.description}}</p>
      <template v-if="todo.startTime">
        <span class="todo__time">
          <!--eslint-disable-next-line max-len -->
          <time :datetime="todo.startTime" class="time-block">{{todo.startTime}}</time> -
          <time :datetime="todo.startTime" class="time-block">{{todo.endTime}}</time>
        </span>
        <br />
      </template>
      <tag v-if="todo.status === 'done' && editMode" variant="green">
        {{t('status')}}: {{t('todos.optional')}}
      </tag>
      <tag v-if="todo.optional">{{t('todos.optional')}}</tag>
      <tag v-if="todo.carryOver" variant="purple">{{t('todos.carryOver')}}</tag>
      <tag v-if="editMode && todo.isPrioritized" variant="red">{{t('todos.prioritize')}}</tag>
    </div>
    <div v-if="editMode" class="todo__el todo__checkbox">
      <checkbox v-model="isChecked" />
    </div>
    <div v-if="!editMode && todo.isPrioritized" class="todo__el">
      <img
        src="@/assets/images/icons/star.svg"
        alt="open"
        class="todo__prioritize"
      />
    </div>
    <add-item
      v-if="showAddItem"
      @close="showAddItem = false; resetAddItemModalValue"
      @updateItem="updateItem"
      mode="edit"
      :listType="listType"
      :todoItem="todoItem"
    />
  </div>
</template>

<script lang="ts">
import {
  defineComponent, PropType, ref, watch,
} from 'vue';
import { cloneDeep } from 'lodash';
import { useI18n } from 'vue-i18n';
import { useStore } from 'vuex';
import {
  AppError, ListDayType, ToDoItemExtension, ToDoItemForm,
} from '@/types/shared';
import Checkbox from '@/components/forms/Checkbox.vue';
import IconButton from '@/components/IconButton.vue';
import AddItem from '@/components/AddItem/AddItem.vue';
import Tag from '@/components/Tag.vue';
import ToolTip from '@/components/ToolTip.vue';
import { createToDoItemReq } from '../AddItem/features/helpers';
import { useAddItemState } from '@/components/AddItem/features/hooks/addItemGlobalState';

export default defineComponent({
  components: {
    Checkbox,
    IconButton,
    AddItem,
    Tag,
    ToolTip,
  },
  props: {
    isArchive: {
      type: Boolean,
      default: false,
    },
    todo: {
      type: Object as PropType<ToDoItemExtension>,
      required: true,
    },
    index: {
      type: Number,
      default: -1,
    },
    isEditMode: {
      type: Boolean,
      default: false,
    },
    isForToday: {
      type: Boolean,
      default: true,
    },
    shouldSaveItem: {
      type: Boolean,
      default: true,
    },
    listType: {
      type: String as PropType<ListDayType>,
      default: null,
    },
    checked: Boolean,
  },
  setup(props, ctx) {
    const store = useStore();
    const { setServerError, setLoading, resetAddItemModalValue } = useAddItemState();
    const { t } = useI18n();
    const isChecked = ref(props.todo.checked);
    const showAddItem = ref(false);
    const todoItem = ref<ToDoItemExtension>(cloneDeep(props.todo));
    const isDone = ref<boolean>(props.todo.status === 'done');
    const classes = ref({
      'todo--done': isDone.value,
    });
    const markText = ref(t(isDone.value ? 'todos.mark' : 'todos.unmark'));
    const editMode = props.isEditMode && !props.isArchive;
    const viewMode = !props.isEditMode && props.isForToday && !props.isArchive;

    watch(isChecked, (newValue) => {
      ctx.emit('setChecked', {
        checked: newValue,
        index: props.index,
      });
    });

    watch(() => props.todo, (newValue) => {
      isChecked.value = newValue.checked;
    });

    const updateItem = async (formData: ToDoItemForm) => {
      if (props.shouldSaveItem === false) {
        showAddItem.value = false;
        setLoading(false);
        ctx.emit('saveLocally', createToDoItemReq(formData));
        return;
      }

      const result = await store.dispatch('todos/update', {
        listType: props.listType,
        item: createToDoItemReq(formData),
      });
      if (result instanceof AppError) {
        setLoading(false);
        setServerError(t('addItemComponent.errors.cannotUpdateItem'));
        return;
      }
      showAddItem.value = false;
      resetAddItemModalValue();
      ctx.emit('itemSaved');
    };

    watch(() => props.todo, (newValue) => {
      todoItem.value = cloneDeep(props.todo);
      isDone.value = newValue.status === 'done';
      classes.value = {
        'todo--done': isDone.value,
      };
      markText.value = t(isDone.value ? 'todos.mark' : 'todos.unmark');
    });

    const setStatus = () => {
      const status = props.todo.status === 'done' ? 'open' : 'done';
      ctx.emit('statusUpdate', {
        id: props.todo.id,
        status,
      });
    };

    return {
      t,
      showAddItem,
      resetAddItemModalValue,
      isChecked,
      isDone,
      todoItem,
      classes,
      setStatus,
      updateItem,
      markText,
      editMode,
      viewMode,
    };
  },
});
</script>

<style lang="scss" scoped>
@import '@/assets/styles/utils/_functions.scss';

.todo {
  @apply flex justify-between;

  &__content {
    @apply flex-1;
  }

  &--done {
    .todo__description {
      @apply text-green-500;
    }
  }

  &__done-btn {
    width: remSize(35px);
    flex: 0 0 remSize(35px);
  }

  &__el {
    &:not(:last-child) {
      @apply mr-3;
    }
  }

  &__checkbox {
    @apply mt-1;
  }

  &__prioritize {
    width: remSize(20px);
  }
}

.todo:deep(.tag) {
  @apply mt-1;
}
</style>
