
import { defineComponent, ref, watch } from 'vue';

export default defineComponent({
  props: {
    selected: {
      type: Boolean,
      default: false,
    },
    bgColor: {
      type: String,
      default: '',
    },
    color: {
      type: String,
      default: '',
    },
  },
  setup(props) {
    const classes = ref<Record<string, boolean>>({
      'rounded-item--selected': props.selected,
    });

    const style = {
      backgroundColor: props.bgColor,
      color: props.color,
    };

    watch(() => props.selected, (newValue) => {
      classes.value['rounded-item--selected'] = newValue;
    });

    return {
      classes,
      style,
    };
  },
});
