
import {
  defineComponent, PropType, ref, watch,
} from 'vue';
import { cloneDeep } from 'lodash';
import { useI18n } from 'vue-i18n';
import { useStore } from 'vuex';
import {
  AppError, ListDayType, ToDoItemExtension, ToDoItemForm,
} from '@/types/shared';
import Checkbox from '@/components/forms/Checkbox.vue';
import IconButton from '@/components/IconButton.vue';
import AddItem from '@/components/AddItem/AddItem.vue';
import Tag from '@/components/Tag.vue';
import ToolTip from '@/components/ToolTip.vue';
import { createToDoItemReq } from '../AddItem/features/helpers';
import { useAddItemState } from '@/components/AddItem/features/hooks/addItemGlobalState';

export default defineComponent({
  components: {
    Checkbox,
    IconButton,
    AddItem,
    Tag,
    ToolTip,
  },
  props: {
    isArchive: {
      type: Boolean,
      default: false,
    },
    todo: {
      type: Object as PropType<ToDoItemExtension>,
      required: true,
    },
    index: {
      type: Number,
      default: -1,
    },
    isEditMode: {
      type: Boolean,
      default: false,
    },
    isForToday: {
      type: Boolean,
      default: true,
    },
    shouldSaveItem: {
      type: Boolean,
      default: true,
    },
    listType: {
      type: String as PropType<ListDayType>,
      default: null,
    },
    checked: Boolean,
  },
  setup(props, ctx) {
    const store = useStore();
    const { setServerError, setLoading, resetAddItemModalValue } = useAddItemState();
    const { t } = useI18n();
    const isChecked = ref(props.todo.checked);
    const showAddItem = ref(false);
    const todoItem = ref<ToDoItemExtension>(cloneDeep(props.todo));
    const isDone = ref<boolean>(props.todo.status === 'done');
    const classes = ref({
      'todo--done': isDone.value,
    });
    const markText = ref(t(isDone.value ? 'todos.mark' : 'todos.unmark'));
    const editMode = props.isEditMode && !props.isArchive;
    const viewMode = !props.isEditMode && props.isForToday && !props.isArchive;

    watch(isChecked, (newValue) => {
      ctx.emit('setChecked', {
        checked: newValue,
        index: props.index,
      });
    });

    watch(() => props.todo, (newValue) => {
      isChecked.value = newValue.checked;
    });

    const updateItem = async (formData: ToDoItemForm) => {
      if (props.shouldSaveItem === false) {
        showAddItem.value = false;
        setLoading(false);
        ctx.emit('saveLocally', createToDoItemReq(formData));
        return;
      }

      const result = await store.dispatch('todos/update', {
        listType: props.listType,
        item: createToDoItemReq(formData),
      });
      if (result instanceof AppError) {
        setLoading(false);
        setServerError(t('addItemComponent.errors.cannotUpdateItem'));
        return;
      }
      showAddItem.value = false;
      resetAddItemModalValue();
      ctx.emit('itemSaved');
    };

    watch(() => props.todo, (newValue) => {
      todoItem.value = cloneDeep(props.todo);
      isDone.value = newValue.status === 'done';
      classes.value = {
        'todo--done': isDone.value,
      };
      markText.value = t(isDone.value ? 'todos.mark' : 'todos.unmark');
    });

    const setStatus = () => {
      const status = props.todo.status === 'done' ? 'open' : 'done';
      ctx.emit('statusUpdate', {
        id: props.todo.id,
        status,
      });
    };

    return {
      t,
      showAddItem,
      resetAddItemModalValue,
      isChecked,
      isDone,
      todoItem,
      classes,
      setStatus,
      updateItem,
      markText,
      editMode,
      viewMode,
    };
  },
});
